.popup {
	position: fixed;
	width: rem(1118);
	height: rem(424);
	border-radius: rem(11);
	background-color: white;
	top: 50%;
	margin-top: rem(-212);
	margin-left: rem(-559);
	left: 50%;

	padding: rem(28) rem(54);

	box-shadow: 0 rem(2) rem(7) rgba(0,0,0,.25);

	z-index: 100;

	display: none;

	@include min1050 {
		width: calc(100% - 40px);
		height: auto;
		left: 0;
		margin-left: 20px;
		margin-right: 20px;
		padding: rem(18) rem(32);
	}

	@include min540 {
		margin-top: rem(-270);
	}

	&_title {
		text-align: center;
		margin-bottom: rem(36);
		h2,h3,h4,h5 {
			display: block;
			font-weight: bold;
			font-size: rem(36);
			background: var(--g-blue);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;

			@include min1050 {
				font-size: rem(24);
			}
		}
	}

	&_form {

		&-field {
			display: flex;
			align-items: center;
			// flex-wrap: wrap;
			margin-bottom: rem(15);

			@include min {
				flex-wrap: wrap;
				margin-bottom: rem(8);
			}

			&:last-child {
				margin-bottom: 0;
			}
			.field_left {
				flex: 0 0 rem(320);

				@include min {
					flex: 0 0 100%;
				}
			}
			.field_group {
				width: 100%;
			}
			label {
				width: rem(190);
				margin-right: rem(135);

				font-weight: 500;
				font-family: var(--f-form);
				font-size: rem(18);

				@include min1050 {
					width: rem(150);
					font-size: rem(16);
				}

				@include min {
					width: 100%;
					margin-bottom: rem(8);
					margin-right: 0;
					display: block;
				}

				@include min540 {
					font-size: rem(14);
				}

				@include min380 {
					font-size: rem(12);
				}
			}

			.field_checked {
				flex-grow: 1;
				flex-shrink: 0;

				margin-right: rem(13);

				@include min {
					width: auto;
				}

				@include min540 {
					width: 100%;
				}

				&:last-child {
					margin-right: 0;
				}

				input {
					display: none;

					&:checked ~ .check {
						background: var(--g-blue);
						color: var(--c-white);
					}
				}

				.check {
					cursor: pointer;
					
					height: rem(55);
					border-radius: rem(11);
					background-color: var(--c-white);
					color: var(--c-blue-b);
					display: flex;
					align-items: center;
					justify-content: center;
					box-shadow: 0 rem(2) rem(7) rgba(0,0,0,.25);
					font-weight: bold;
					font-family: var(--f-form);
					font-size: rem(18);

					@include min1050 {
						font-size: rem(14);
					}

					@include min380 {
						font-size: rem(12);
					}
				}
			}
		}

		.button_blue {

			height: rem(50);
			font-size: rem(18);
			font-family: var(--f-form);

			margin-top: rem(40);
			margin-left: auto;
			margin-right: auto;

			@include min1050 {
				font-size: rem(14);
			}
		}
	}
}

.popup.active {
	display: block;
}