.footer {
	padding-top: rem(52);
	padding-bottom: rem(64);

	background: url(../img/footer_bg.jpg) no-repeat top center;
	background-size: cover;

	@include min768 {
		padding-top: rem(32);
		padding-bottom: rem(44);
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&_left {

		ul {

			li {
				font-size: rem(20);
				font-weight: 300;
				color: var(--c-white);

				@include min768 {
					font-size: rem(16);
				}

				&:first-child {
					margin-bottom: rem(28);

					@include min768 {
						margin-bottom: rem(14);
					}
				}

				&:last-child {
					margin-top: rem(28);

					@include min768 {
						margin-top: rem(14);
					}
				}

				a {
					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}

	&_right {
		@include min540 {
			margin-top: rem(24);
		}
		
		b {
			font-weight: bold;
			margin-bottom: rem(20);
			font-size: rem(18);
			color: var(--c-white);
			display: block;

			@include min768 {
				font-size: rem(16);
			}
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(20);

			li {
				margin-right: rem(10);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}