.header {
	position: relative;
	width: 100%;
	background: url(../img/header_bg.jpg) no-repeat top center;
	background-size: cover;

	padding-bottom: rem(87);

	@include min540 {
		background: url(../img/header_bg_x1.jpg) no-repeat top center;
		background-size: 100%;
		padding-bottom: rem(30);
	}

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: rem(550);
		background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
		// z-index: ;

		// @include min540 {
		// 	background: url(../img/header_bg_x1.jpg) no-repeat top center;
		// 	background-size: cover;
		// 	height: rem(670);
		// 	top: 0;
		// 	bottom: auto;
		// 	z-index: -1;
		// }
	}
	
	&_top {
		padding-top: rem(26);

		margin-right: rem(-130);
		margin-bottom: rem(106);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		@include min1360 {
			margin-right: 0;
			padding-top: rem(18);
			margin-bottom: rem(72);
		}

		&-logo {
			max-width: rem(165);
			margin-right: rem(51);

			@include min540 {
				max-width: rem(71);
				margin-right: rem(30);
			}
		}

		&-menu {
			margin-right: rem(63);

			@include min1360 {
				display: none;
			}

			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(50);

					&:last-child {
						margin-right: 0;
					}

					a {
						transition: all 100ms ease;
						font-size: rem(18);
						color: #878787;

						&:hover {
							color: #5A5A5A;
							text-shadow: 0 0 1px #5A5A5A,
							0 0 1px #5A5A5A;
						}
					}
				}
			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.text {
				margin-right: rem(26);
				color: var(--c-blue-b);

				@include min {
					margin-right: 0;
					text-align: right;
				}

				@include min540 {
					font-size: rem(12);
				}

				b {
					margin-right: rem(3);

					@include min {
						display: block;
					}
				}
			}

			.button_green {
				width: rem(170);

				@include min {
					display: none;
				}
			}
		}
	}

	&_wrap {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(106);

		@include min540 {
			margin-bottom: rem(60);
		}

		&-left, &-right {
			max-width: 50%;
			flex-basis: 50%;

			@include min660 {
				max-width: 100%;
				flex-basis: 100%;
			}
		}

		&-left {
			padding-right: rem(100);
			@include min {
				padding-right: 0;
				margin-bottom: rem(32);
			}
			h1 {
				font-size: rem(36);
				font-weight: bold;
				color: var(--c-blue-b);
				margin-bottom: rem(20);

				@include min {
					font-size: rem(24);
					max-width: rem(278);

					margin-left: rem(12);
					margin-right: rem(12);
				}
			}

			p {
				color: #686868;
				font-size: rem(24);
				font-weight: 300;
				margin-bottom: rem(45);

				@include min {
					font-size: rem(14);
					margin-bottom: rem(40);

					margin-left: rem(12);
					margin-right: rem(12);
				}
			}
		}

		&-right {
			img {
				width: 100%;
				object-fit: cover;
			}
		}
	}

	&_bottom {
		position: relative;
		z-index: 1;
		&-top {
			text-align: center;
			margin-bottom: rem(45);

			@include min540 {
				margin-bottom: rem(20);
			}

			h2 {
				// font-size: rem(32);
				font-weight: bold;
				color: var(--c-blue-b);
				display: block;
				margin-bottom: rem(8);
			}

			p {
				font-size: rem(22);
				font-weight: 300;
				max-width: rem(563);

				margin-left: auto;
				margin-right: auto;

				@include min540 {
					font-size: rem(14);
					max-width: 100%;
				}
			}
		}

		&-c {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			img {
				margin-right: rem(45);

				@include min1200 {
					width: 50%;
					margin-right: rem(20);
				}

				@include min {
					margin-right: auto;
					margin-left: auto;
					margin-bottom: rem(20);
				}

				@include min768 {
					width: 100%;
				}
			}

			> .flex {
				max-width: 45%;
				flex-basis: 45%;

				@include min768 {
					max-width: 100%;
					flex-basis: 100%;
				}

				p {
					font-size: rem(20);
					font-weight: 300;
					margin-bottom: rem(30);

					@include min540 {
						font-size: rem(14);
						margin-bottom: rem(18);
					}

					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
	}
}