:root {
	--f-default: 'Gilroy';
	--f-form: 'Montserrat';
	--g-green: linear-gradient(180deg, #56E386 0%, #19D787 100%);
	--g-green-h: linear-gradient(180deg, #19D787 0%, #19D787 100%);
	--g-blue: linear-gradient(180deg, #56D2E3 0%, #1991D7 100%);
	--g-blue-h: linear-gradient(180deg, #1991D7 0%, #1991D7 100%);
	--c-white: #FFFFFF;
	--c-gray: #393939;
	--c-blue: #1991D7;
	--c-blue-b: #0C527B;
}


* {
	padding: 0;
	margin: 0;
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

h1 {
	font-weight: bold;
	font-size: rem(36);
}

h2,h3,h4,h5 {
	font-weight: bold;
	font-size: rem(32);

	@include min {
		font-size: rem(24);
	}
}

p {
	font-weight: 300;
}

button {
	background-color: transparent;
	cursor: pointer;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: var(--f-default);
	font-size: rem(16);
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.relative {
	position: relative;
}

.container {
	max-width: 1080px;
	margin: 0 auto;

	@include min1200 {
		max-width: 100%;
		margin-left: 20px;
		margin-right: 20px;
	}
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.button_green {
	height: rem(54);
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: rem(64);
	background: var(--g-green);

	font-weight: bold;
	font-size: rem(20);
	color: var(--c-white);

	&:hover {
		background: var(--g-green-h);
	}
}

.button_blue {
	width: rem(324);
	height: rem(67);
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
	font-size: rem(20);
	color: var(--c-white);
	background: var(--g-blue);
	border-radius: rem(52);

	// transition: all 200ms ease;

	@include min {
		width: rem(226);
		font-size: rem(14);
		height: rem(47);
	}

	&:hover {
		background: var(--g-blue-h);
	}
}

.button_border {
	// width: rem(2);
	height: rem(52);
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: rem(18);
	color: #F3F3F3;
	// background: var(--g-blue);
	border: rem(2) solid white;
	border-radius: rem(60);
	padding: 0 rem(30);

	transition: all 200ms ease;

	@include min {
		// width: rem(226);
		font-size: rem(14);
		height: rem(47);
	}

	&:hover {
		background-color: white;
		color: var(--c-blue);
	}
}

.field_group {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.field_set {
		width: rem(300);

		@include min768 {
			width: 100%;
		}
	}
}

.field_set {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(16);

	&:last-child {
		margin-bottom: 0;
	}

	&-label {
		font-weight: bold;
		font-size: rem(18);
		color: #747474;
		margin-bottom: rem(9);

		@include min768 {
			font-size: rem(14);
			margin-bottom: rem(6);
		}
	}
}

.field {
	background-color: white;
	border-radius: rem(9);
	width: 100%;
	height: rem(60);
	box-shadow: inset 0 rem(1) rem(7) rgba(0,0,0,.2);

	text-align: center;
	color: #0068B5;
	font-size: rem(18);
	font-weight: bold;
	font-family: var(--f-form);

	@include min768 {
		font-size: rem(16);
		height: rem(50);
	}

	@include placeholder {
		color: #0068B5;
	}
}