.statistic {
	background: url(../img/metrics_bg.jpg) no-repeat top center;
	background-size: cover;
	padding-top: rem(52);
	padding-bottom: rem(72);

	@include min1200 {
		display: none;
	}

	&_title {
		margin-bottom: rem(42);

		h2,h3,h4 {
			font-weight: bold;
			color: var(--c-white);
		}
	}

	&_items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.item {
			width: calc((100% - 7.25rem) / 3);
			margin-right: rem(58);

			&:nth-child(2n) {
				padding-left: rem(15);
			}

			&:last-child {
				margin-right: 0;
				
				padding-left: rem(30);
			}

			b {
				font-size: rem(78);
				font-weight: bold;
				color: var(--c-white);
			}

			p {
				font-weight: bold;
				color: var(--c-white);
				max-width: rem(250);
			}
		}
	}
}