*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Light"), local("Gilroy-Light"), url("../fonts/Gilroy/Gilroy-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Regular"), local("Gilroy-Regular"), url("../fonts/Gilroy/Gilroy-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Medium"), local("Gilroy-Medium"), url("../fonts/Gilroy/Gilroy-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: local("Gilroy Bold"), local("Gilroy-Bold"), url("../fonts/Gilroy/Gilroy-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat/Montserrat-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

:root {
  --f-default: 'Gilroy';
  --f-form: 'Montserrat';
  --g-green: linear-gradient(180deg, #56E386 0%, #19D787 100%);
  --g-green-h: linear-gradient(180deg, #19D787 0%, #19D787 100%);
  --g-blue: linear-gradient(180deg, #56D2E3 0%, #1991D7 100%);
  --g-blue-h: linear-gradient(180deg, #1991D7 0%, #1991D7 100%);
  --c-white: #FFFFFF;
  --c-gray: #393939;
  --c-blue: #1991D7;
  --c-blue-b: #0C527B;
}

* {
  padding: 0;
  margin: 0;
  outline: none;
  border: 0;
}

html, body {
  position: relative;
  overflow-x: hidden;
}

html {
  font-size: 100%;
}

h1 {
  font-weight: bold;
  font-size: 2.25rem;
}

h2, h3, h4, h5 {
  font-weight: bold;
  font-size: 2rem;
}

@media (max-width: 899px) {
  h2, h3, h4, h5 {
    font-size: 1.5rem;
  }
}

p {
  font-weight: 300;
}

button {
  background-color: transparent;
  cursor: pointer;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  position: relative;
  font-family: var(--f-default);
  font-size: 1rem;
  overflow-x: hidden;
}

.relative {
  position: relative;
}

.container {
  max-width: 1080px;
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    margin-left: 20px;
    margin-right: 20px;
  }
}

.w-8\/12 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.w-7\/12 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%;
}

.w-6\/12 {
  flex: 0 0 50%;
  max-width: 50%;
}

.w-5\/12 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.w-4\/12 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-col {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.items-center {
  align-items: center !important;
}

.items-start {
  align-items: flex-start !important;
}

.items-end {
  align-items: flex-end !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.button_green {
  height: 3.375rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4rem;
  background: var(--g-green);
  font-weight: bold;
  font-size: 1.25rem;
  color: var(--c-white);
}

.button_green:hover {
  background: var(--g-green-h);
}

.button_blue {
  width: 20.25rem;
  height: 4.1875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.25rem;
  color: var(--c-white);
  background: var(--g-blue);
  border-radius: 3.25rem;
}

@media (max-width: 899px) {
  .button_blue {
    width: 14.125rem;
    font-size: 0.875rem;
    height: 2.9375rem;
  }
}

.button_blue:hover {
  background: var(--g-blue-h);
}

.button_border {
  height: 3.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  color: #F3F3F3;
  border: 0.125rem solid white;
  border-radius: 3.75rem;
  padding: 0 1.875rem;
  transition: all 200ms ease;
}

@media (max-width: 899px) {
  .button_border {
    font-size: 0.875rem;
    height: 2.9375rem;
  }
}

.button_border:hover {
  background-color: white;
  color: var(--c-blue);
}

.field_group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.field_group .field_set {
  width: 18.75rem;
}

@media (max-width: 767px) {
  .field_group .field_set {
    width: 100%;
  }
}

.field_set {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.field_set:last-child {
  margin-bottom: 0;
}

.field_set-label {
  font-weight: bold;
  font-size: 1.125rem;
  color: #747474;
  margin-bottom: 0.5625rem;
}

@media (max-width: 767px) {
  .field_set-label {
    font-size: 0.875rem;
    margin-bottom: 0.375rem;
  }
}

.field {
  background-color: white;
  border-radius: 0.5625rem;
  width: 100%;
  height: 3.75rem;
  box-shadow: inset 0 0.0625rem 0.4375rem rgba(0, 0, 0, 0.2);
  text-align: center;
  color: #0068B5;
  font-size: 1.125rem;
  font-weight: bold;
  font-family: var(--f-form);
}

@media (max-width: 767px) {
  .field {
    font-size: 1rem;
    height: 3.125rem;
  }
}

.field::-webkit-input-placeholder {
  color: #0068B5;
}

.field:-moz-placeholder {
  color: #0068B5;
}

.field::-moz-placeholder {
  color: #0068B5;
}

.field:-ms-input-placeholder {
  color: #0068B5;
}

.header {
  position: relative;
  width: 100%;
  background: url(../img/header_bg.jpg) no-repeat top center;
  background-size: cover;
  padding-bottom: 5.4375rem;
}

@media (max-width: 540px) {
  .header {
    background: url(../img/header_bg_x1.jpg) no-repeat top center;
    background-size: 100%;
    padding-bottom: 1.875rem;
  }
}

.header::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 34.375rem;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, white 100%);
}

.header_top {
  padding-top: 1.625rem;
  margin-right: -8.125rem;
  margin-bottom: 6.625rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1360px) {
  .header_top {
    margin-right: 0;
    padding-top: 1.125rem;
    margin-bottom: 4.5rem;
  }
}

.header_top-logo {
  max-width: 10.3125rem;
  margin-right: 3.1875rem;
}

@media (max-width: 540px) {
  .header_top-logo {
    max-width: 4.4375rem;
    margin-right: 1.875rem;
  }
}

.header_top-menu {
  margin-right: 3.9375rem;
}

@media (max-width: 1360px) {
  .header_top-menu {
    display: none;
  }
}

.header_top-menu ul {
  display: flex;
  flex-wrap: wrap;
}

.header_top-menu ul li {
  margin-right: 3.125rem;
}

.header_top-menu ul li:last-child {
  margin-right: 0;
}

.header_top-menu ul li a {
  transition: all 100ms ease;
  font-size: 1.125rem;
  color: #878787;
}

.header_top-menu ul li a:hover {
  color: #5A5A5A;
  text-shadow: 0 0 1px #5A5A5A, 0 0 1px #5A5A5A;
}

.header_top-right {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header_top-right .text {
  margin-right: 1.625rem;
  color: var(--c-blue-b);
}

@media (max-width: 899px) {
  .header_top-right .text {
    margin-right: 0;
    text-align: right;
  }
}

@media (max-width: 540px) {
  .header_top-right .text {
    font-size: 0.75rem;
  }
}

.header_top-right .text b {
  margin-right: 0.1875rem;
}

@media (max-width: 899px) {
  .header_top-right .text b {
    display: block;
  }
}

.header_top-right .button_green {
  width: 10.625rem;
}

@media (max-width: 899px) {
  .header_top-right .button_green {
    display: none;
  }
}

.header_wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 6.625rem;
}

@media (max-width: 540px) {
  .header_wrap {
    margin-bottom: 3.75rem;
  }
}

.header_wrap-left, .header_wrap-right {
