  }
}

.header_wrap-left {
  padding-right: 6.25rem;
}

@media (max-width: 899px) {
  .header_wrap-left {
    padding-right: 0;
    margin-bottom: 2rem;
  }
}

.header_wrap-left h1 {
  font-size: 2.25rem;
  font-weight: bold;
  color: var(--c-blue-b);
  margin-bottom: 1.25rem;
}

@media (max-width: 899px) {
  .header_wrap-left h1 {
    font-size: 1.5rem;
    max-width: 17.375rem;
    margin-left: 0.75rem;
    margin-right: 0.75rem;
  }
}

.header_wrap-left p {
  color: #686868;
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 2.8125rem;
}

@media (max-width: 899px) {
  .header_wrap-left p {
    font-size: 0.875rem;
    margin-bottom: 2.5rem;
    margin-left: 0.75rem;
    margin-right: 0.75rem;
  }
}

.header_wrap-right img {
  width: 100%;
  object-fit: cover;
}

.header_bottom {
  position: relative;
  z-index: 1;
}

.header_bottom-top {
  text-align: center;
  margin-bottom: 2.8125rem;
}

@media (max-width: 540px) {
  .header_bottom-top {
    margin-bottom: 1.25rem;
  }
}

.header_bottom-top h2 {
  font-weight: bold;
  color: var(--c-blue-b);
  display: block;
  margin-bottom: 0.5rem;
}

.header_bottom-top p {
  font-size: 1.375rem;
  font-weight: 300;
  max-width: 35.1875rem;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 540px) {
  .header_bottom-top p {
    font-size: 0.875rem;
    max-width: 100%;
  }
}

.header_bottom-c {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header_bottom-c img {
  margin-right: 2.8125rem;
}

@media (max-width: 1200px) {
  .header_bottom-c img {
    width: 50%;
    margin-right: 1.25rem;
  }
}

@media (max-width: 899px) {
  .header_bottom-c img {
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 1.25rem;
  }
}

@media (max-width: 767px) {
  .header_bottom-c img {
    width: 100%;
  }
}

.header_bottom-c > .flex {
  max-width: 45%;
  flex-basis: 45%;
}

@media (max-width: 767px) {
  .header_bottom-c > .flex {
    max-width: 100%;
    flex-basis: 100%;
  }
}

.header_bottom-c > .flex p {
  font-size: 1.25rem;
  font-weight: 300;
  margin-bottom: 1.875rem;
}

@media (max-width: 540px) {
  .header_bottom-c > .flex p {
    font-size: 0.875rem;
    margin-bottom: 1.125rem;
  }
}

.header_bottom-c > .flex p:last-child {
  margin-bottom: 0;
}

.services {
  padding-bottom: 7.375rem;
}

@media (max-width: 540px) {
  .services {
    padding-bottom: 3.125rem;
  }
}

.services > .container {
  display: flex;
  flex-wrap: wrap;
}

.services_left {
  max-width: 42.375rem;
  flex-basis: 42.375rem;
}

@media (max-width: 1200px) {
  .services_left {
    max-width: 50%;
    flex-basis: 50%;
  }
}

@media (max-width: 899px) {
  .services_left {
    max-width: 100%;
    flex-basis: 100%;
  }
}

.services_left h2 {
  margin-bottom: 4.375rem;
  background: var(--g-blue);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 767px) {
  .services_left h2 {
    margin-bottom: 1.875rem;
  }
}

.services_left ul li {
  position: relative;
  font-size: 1.25rem;
  color: var(--c-gray);
  margin-bottom: 1.75rem;
}

@media (max-width: 540px) {
  .services_left ul li {
    font-size: 0.875rem;
    margin-bottom: 1.125rem;
  }
}

.services_left ul li::before {
  content: "";
  position: absolute;
  top: 0.625rem;
  left: -0.625rem;
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  background-color: var(--c-gray);
}

@media (max-width: 540px) {
  .services_left ul li::before {
    top: 0.4375rem;
  }
}

.services_left ul li:last-child {
  margin-bottom: 0;
}

.services_right {
  margin-top: 3.625rem;
  position: relative;
}

@media (max-width: 1200px) {
  .services_right {
    margin-left: auto;
  }
}

@media (max-width: 899px) {
  .services_right {
    display: none;
  }
}

.services_right::before {
  content: "";
  position: absolute;
  left: 3rem;
  bottom: -3.625rem;
  width: 30.25rem;
  height: 20.375rem;
  background: url(../img/services_shadow.png) no-repeat;
  background-size: 100%;
  z-index: -1;
}

@media (max-width: 1200px) {
  .services_right::before {
    bottom: 3.625rem;
  }
}

.program {
  width: 100%;
  background: url(../img/program_bg.jpg) no-repeat top center;
  background-size: cover;
  padding-bottom: 9.125rem;
}

@media (max-width: 767px) {
  .program {
    position: relative;
    background: none;
    background-size: cover;
    padding-bottom: 0;
  }
  .program::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 6.25rem;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #B8CEF4 100%);
    z-index: -1;
  }
  .program::before {
    content: "";
    position: absolute;
    top: -28.125rem;
    left: 0;
    width: 100%;
    background: url(../img/program_bg_x1.jpg) no-repeat top center;
    background-size: cover;
    height: 60.625rem;
    z-index: -1;
  }
}

.program_top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4.375rem;
}

@media (max-width: 767px) {
  .program_top {
    margin-bottom: 2rem;
  }
}

@media (max-width: 767px) {
  .program_top-left {
    text-align: center;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

.program_top-left h2 {
  color: var(--c-blue-b);
  margin-bottom: 0.5rem;
}

.program_top-left p {
  font-size: 1.25rem;
}

@media (max-width: 899px) {
  .program_top-left p {
    font-size: 0.875rem;
  }
}

@media (max-width: 767px) {
  .program_top-right {
    display: none;
  }
}

.program_items {
  display: flex;
  flex-wrap: wrap;
}

.program_items .smart {
  margin-left: auto;
  margin-right: auto;
  margin-top: 3.75rem;
}

@media (min-width: 900px) {
  .program_items .smart {
    display: none;
  }
}

@media (max-width: 899px) {
  .program_items .swiper-container {
    margin: -40px -30px;
    padding: 40px 30px;
  }
}

@media (min-width: 900px) {
  .program_items .swiper-container {
    overflow: visible;
  }
  .program_items .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
  }
