    display: none;
  }
}

.program_items .swiper-pagination-bullet {
  width: 0.75rem;
  height: 0.75rem;
}

.program_items .swiper-pagination-bullet-active {
  background: var(--c-blue);
}

.program_items .item {
  border-radius: 1.4375rem;
  background-color: white;
