.message {
	padding-bottom: rem(113);

	background: url(../img/message_bg.jpg) no-repeat top center;
	background-size: cover;

	@include min768 {
		padding-bottom: rem(64);
	}

	&_title {
		text-align: center;
		margin-bottom: rem(58);

		@include min768 {
			margin-bottom: rem(24);
		}

		h2,h3,h4 {
			color: var(--c-blue-b);
			margin-bottom: rem(12);

		}

		p {
			font-size: rem(20);

			@include min768 {
				font-size: rem(14);
			}
		}
	}

	&_form {
		background-color: white;
		padding: rem(49) rem(60) rem(45) rem(60);
		border-radius: rem(23);

		box-shadow: rem(5) rem(5) rem(33) rgba(0,0,0,.15);

		@include min768 {
			padding: rem(28) rem(24) rem(28) rem(24);
			border-radius: rem(15);
			box-shadow: rem(4) rem(4) rem(14) rgba(0, 0, 0, .15);

			// .field_set {
			// 	width: 100%;
			// }
		}

		.button_blue {
			margin-top: rem(22);
			margin-left: auto;
			margin-right: auto;
		}
	}
}