.history {
	padding-top: rem(22);
	padding-bottom: rem(78);
	
	background: url(../img/history_bg.jpg) no-repeat top center;
	background-size: cover;

	@include min768 {
		padding-bottom: rem(36);
	}

	&_title {
		margin-bottom: rem(48);

		@include min540 {
			margin-bottom: rem(20);
		}

		h2,h3,h4 {
			color: var(--c-blue-b);
			margin-bottom: rem(14);

			@include min540 {
				text-align: center;
			}
		}

		p {
			font-size: rem(20);

			@include min540 {
				text-align: center;
				font-size: rem(14);
			}
		}
	}

	&_wrap {
		border-radius: rem(23);
		padding: rem(50) rem(56);
		background-color: white;
		box-shadow: rem(5) rem(5) rem(33) rgba(0,0,0,.15);

		@include min {
			border-radius: rem(15);
			padding: rem(20) rem(26);
			box-shadow: rem(4) rem(4) rem(14) rgba(0, 0, 0, .15);
		}

		&-top {
			margin-bottom: rem(30);

			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			@include min {
				.button_blue {
					margin-top: rem(20);
					margin-left: auto;
					margin-right: auto;
				}
			}

			.left {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				@include min768 {
		
				}

				.image {
					border-radius: 50%;
					width: rem(122);
					height: rem(122);
					display: flex;
					align-items: center;
					justify-content: center;
					overflow: hidden;
					background: var(--g-blue);

					margin-right: rem(32);

					@include min {
						width: rem(56);
						height: rem(56);
						margin-right: rem(16);
					}

					img {
						width: rem(108);
						height: rem(108);
						border-radius: 50%;
						object-fit: cover;

						@include min {
							width: rem(50);
							height: rem(50);
						}
					}
				}

				.text {
					@include min {
						flex: 0 0 70%;
					}
					.title {
						margin-bottom: rem(8);

						@include min1050 {
							font-size: rem(18);
						}

						@include min {
							font-size: rem(16);
						}
					}

					p {
						font-size: rem(20);

						@include min1050 {
							font-size: rem(16);
							max-width: rem(200);
						}

						@include min {
							font-size: rem(14);
							max-width: 100%;
						}
					}
				}
			}
		}

		&-bottom {

			@include min540 {
				font-size: rem(14);
			}

			> .flex {
				margin-bottom: rem(32);

				@include min {
					margin-bottom: rem(16);
				}
			}

			.item {
				display: flex;
				flex-direction: column;

				@include min {
					margin-bottom: rem(20);
				}

				b {
					margin-bottom: rem(12);
					color: var(--c-gray);
					font-size: rem(20);

					@include min {
						font-size: rem(16);
					}
				}

				span {
					font-size: rem(32);
					font-weight: bold;
					color: var(--c-blue);

					@include min {
						font-size: rem(24);
					}
				}
			}
		}
	}
}