.services {
	padding-bottom: rem(118);

	@include min540 {
		padding-bottom: rem(50);
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
	}
	&_left {
		max-width: rem(678);
		flex-basis: rem(678);

		@include min1200 {
			max-width: 50%;
			flex-basis: 50%;
		}

		@include min {
			max-width: 100%;
			flex-basis: 100%;
		}

		h2 {
			margin-bottom: rem(70);

			background: var(--g-blue);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;

			@include min768 {
				margin-bottom: rem(30);
			}
		}

		ul {
			li {
				position: relative;
				font-size: rem(20);
				color: var(--c-gray);
				margin-bottom: rem(28);

				@include min540 {
					font-size: rem(14);
					margin-bottom: rem(18);
				}

				&::before {
					content: "";
					position: absolute;
					top: rem(10);
					left: rem(-10);
					width: rem(4);
					height: rem(4);
					border-radius: 50%;
					background-color: var(--c-gray);

					@include min540 {
						top: rem(7);
					}
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	&_right {
		margin-top: rem(58);
		position: relative;

		@include min1200 {
			margin-left: auto;
		}

		@include min {
			display: none;
		}

		&::before {
			content: "";
			position: absolute;
			// top: rem(117);
			left: rem(48);
			bottom: rem(-58);
			// display: block;
			width: rem(484);
			height: rem(326);
			background: url(../img/services_shadow.png) no-repeat;
			background-size: 100%;
			z-index: -1;

			@include min1200 {
				bottom: rem(58);
			}
		}
	}
}