.program {
	width: 100%;
	background: url(../img/program_bg.jpg) no-repeat top center;
	background-size: cover;

	padding-bottom: rem(146);

	@include min768 {
		position: relative;
		background: none;
		background-size: cover;

		padding-bottom: 0;

		&::after {
			content: "";
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: rem(100);
			background: linear-gradient(180deg, rgba(255,255,255,0) 0%, #B8CEF4 100%);
			z-index: -1;
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(-450);
			left: 0;
			width: 100%;
			background: url(../img/program_bg_x1.jpg) no-repeat top center;
			background-size: cover;
			height: rem(970);
			z-index: -1;
		}
	}

	&_top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin-bottom: rem(70);

		@include min768 {
			margin-bottom: rem(32);

			
		}

		&-left {

			@include min768 {
				text-align: center;
				margin-bottom:0;

				margin-left: auto;
				margin-right: auto;
			}

			h2 {
				color: var(--c-blue-b);
				margin-bottom: rem(8);
			}

			p {
				font-size: rem(20);
				
				@include min {
					font-size: rem(14);
				}
			}
		}

		&-right {
			@include min768 {
				display: none;
			}
		}
	}

	&_items {
		display: flex;
		flex-wrap: wrap;

		.smart {
			margin-left: auto;
			margin-right: auto;
			margin-top: rem(60);
		}

		@include mw900 {
			.smart {
				display: none;
			}

			
		}

		@include min {
			.swiper-container {
				margin: -40px -30px;
				padding: 40px 30px;
			}
		}

		@include mw900 {
			.swiper-container {
				overflow: visible;
			}

			.swiper-wrapper {
				display: flex;
				flex-wrap: wrap;
			}

			.swiper-slide {
				// width: 100%;
				width: calc((100% - 3rem) / 3);
				height: auto;
				margin-right: rem(23);
				margin-bottom: rem(17);

				&:nth-child(3n) {
					margin-right: 0;
				}

				.item {
					width: 100%;
					height: 100%;

					
				}
			}
		}

		.swiper-pagination {
			position: relative;
			bottom: auto !important;
			margin-top: rem(20);
			z-index: 10;

			@include mw900 {
				display: none;
			}
		}

		.swiper-pagination-bullet {
			width: rem(12);
			height: rem(12);
		}

		.swiper-pagination-bullet-active {
			background: var(--c-blue);
		}
	
		.item {
			// width: calc((100% - 3rem) / 3);
			// margin-right: rem(23);

			border-radius: rem(23);
			background-color: white;
			padding: rem(19) rem(16);

			box-shadow: rem(5) rem(5) rem(33) rgba(0,0,0,.15);

			// margin-bottom: rem(17);

			display: flex;
			flex-direction: column;

			

			@include min {
				// width: calc((100% - 3rem) / 2);
				box-shadow: rem(4) rem(4) rem(7) rgba(0, 0, 0, .15);

				position: relative;

				&::before {
					content: "";
					position: absolute;
					top: rem(6);
					left: rem(10);
					border-radius: rem(23);
					background-color: white;
					width: 100%;
					height: 100%;
					z-index: -1;
				}

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@include min768 {
				width: 100%;
				margin-right: 0;
			}

			@include min {
				height: 100%;
			}

			&:nth-child(3n) {
				margin-right: 0;

				@include min {
					margin-right: rem(23);
				}

				@include min768 {
					margin-right: 0;
				}
			}

			.title, .price {
				font-size: rem(20);
				display: block;

				@include min540 {
					font-size: rem(18);
				}
			}

			.title {
				color: var(--c-gray);
				// font-size: rem(20);
				margin-bottom: rem(7);
				// max-width: rem(292);

				@include min768 {
					margin-bottom: rem(24);
				}
			}
			.price {
				color: var(--c-blue);
				margin-bottom: rem(26);

				@include min768 {
					margin-bottom: 0;
				}
			}

			.subtitle {
				margin-bottom: rem(16);

				
			}

			p {
				margin-top: auto;

				@include min540 {
					font-size: rem(14);
				}
			}
		}
	}
}