.grant {
	padding-top: rem(78);
	padding-bottom: rem(71);

	background: url(../img/grant_bg.jpg) no-repeat top 2rem right -2rem;
	background-size: cover;

	@include min540 {
		padding-top: 0;
		background: none;
	}

	&_title {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: rem(54);

		@include min540 {
			margin-bottom: rem(28);
		}

		h2,h3,h4 {
			color: var(--c-blue-b);
			margin-bottom: rem(20);

			@include min540 {
				text-align: center;
			}
		}

		p {
			font-size: rem(20);
			max-width: rem(760);
			text-align: center;

			@include min540 {
				font-size: rem(14);
			}
		}
	}

	&_wrap {
		display: flex;
		flex-wrap: wrap;

		.smart {
			display: none;
		}

		@include min {
			.smart {
				display: block;
				margin-top: rem(45);
				margin-left: auto;
				margin-right: auto;
			}
		}

		&-items, &-form {
			max-width: 50%;
			flex: 0 0 50%;

			@include min {
				max-width: 100%;
				flex: 0 0 100%;
			}
		}

		&-items {
			padding-right: rem(24);
			margin-top: rem(36);

			@include min540 {
				margin-top: 0;
			}

			.item {
				display: flex;
				// flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(82);

				@include min540 {
					margin-bottom: rem(36);
				}

				&:last-child {
					margin-bottom: 0;
				}

				.image {
					width: rem(72);
					height: rem(72);

					flex: 0 0 rem(72);
					border-radius: rem(23);
					background: var(--g-blue);

					display: flex;
					align-items: center;
					justify-content: center;

					margin-right: rem(20);

					@include min {
						width: rem(50);
						height: rem(50);

						flex: 0 0 rem(50);
						border-radius: rem(15);
					}
				}

				.text {
					b, p {
						display: block;
						font-size: rem(20);

						// @include min540 {
						// 	max-width: rem(200);
						// }
					}

					b {
						font-weight: bold;
						margin-bottom: rem(12);

						@include min540 {
							font-size: rem(16);
							margin-bottom: rem(6);
						}
					}

					p {
						// max-width: rem(393);
						@include min540 {
							font-size: rem(14);
							
						}
					}
				}
			}
		}

		&-form {
			border-radius: rem(23);
			box-shadow: rem(5) rem(5) rem(33) rgba(0,0,0,.1);
			background-color: white;
			padding: rem(45);
			flex: 0 0 48%;
			max-width: 48%;
			margin-left: auto;

			@include min {
				display: none;
			}

			.title {
				text-align: center;
				font-weight: bold;
				font-size: rem(20);
				display: block;

				margin-bottom: rem(26);
			}

			p {
				margin-bottom: rem(24);
				text-align: center;
			}

			.button_blue {
				margin-top: rem(48);
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
}