.works {
	padding-top: rem(18);
	padding-bottom: rem(65);

	background: url(../img/works_bg.jpg) no-repeat top center;
	background-size: cover;

	@include min540 {
		background: url(../img/works_bg_x1.jpg) no-repeat top center;
		background-size: cover;

		padding-top: rem(42);
	}

	@include min {
		padding-bottom: 0;
		position: relative;
		// padding-bottom: rem(32);
		.button_blue {
			display: none;
		}

		&::after {
			content: "";
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: rem(100);
			background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
			z-index: 0;
		}
	}

	&_top {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: rem(62);

		@include min {
			margin-bottom: rem(50);
		}

		h2,h3 {
			color: var(--c-blue-b);
			margin-bottom: rem(13);

			@include min540 {
				text-align: center;
			}
		}

		p {
			font-size: rem(20);

			@include min540 {
				font-size: rem(14);
				text-align: center;
				max-width: rem(278);
			}
		}
	}

	&_items {
		margin-bottom: rem(70);

		display: flex;
		flex-direction: column;
		align-items: center;

		@include min {
			margin-bottom: rem(30);
		}

		.item {
			display: flex;
			// flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			width: rem(866);
			height: rem(88);
			border-radius: rem(23);
			background-color: white;
			margin-bottom: rem(23);

			padding: 0 rem(40);

			box-shadow: rem(5) rem(5) rem(33) rgba(0, 0, 0, .10);

			@include min {
				position: relative;
				z-index: 1;
				width: 100%;
				height: auto;

				padding: rem(15) rem(6);
			}

			&:last-child {
				margin-bottom: 0;
			}

			b {
				font-size: rem(24);
				font-weight: bold;
				white-space: nowrap;
				margin-right: rem(42);

				background: var(--g-blue);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;

				@include min {
					margin-left: rem(22);
					margin-right: rem(22);
					span {
						display: none;
					}
				}
			}

			p {
				font-weight: 500;
				font-size: rem(18);
				color: var(--c-gray);

				@include min {
					font-size: rem(14);
				}
			}
		}
	}

	.button_blue {
		margin-left: auto;
		margin-right: auto;
	}
}